.Modal {
    position: absolute;
    background-color: white;
    z-index: 500; 
    width: 70%;
    border: 1px solid #ccc;
    -webkit-box-shadow: 1px 1px 1px grey;
            box-shadow: 1px 1px 1px grey;
    padding: 10px; 
    left: 15%;
    top: 10%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; 
    -webkit-transition: all 0.3x ease-out; 
    -o-transition: all 0.3x ease-out; 
    transition: all 0.3x ease-out;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px; 
        left: calc(50% - 250px);
    }
}



