.Backdrop {
    position: fixed; 
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.3);
    z-index: 100;
}

@media(min-width: 599px) {
    .Backdrop {
        display: none;
    }
}


